/**
 * Element Base
 * @name  Base
 * @class  Base
 * @example
 *  import Base from './base';
 * 
 *  class Path extends Base {
 *    constructor(option) {
 *      super.initialize(option);
 *    }
 *  }
 * 
 */
class Base {
  /**
   *     .
   * @memberof Base
   * @function intialize
   * @param {Object} opt shape 
   */
  initialize(opt) {
    let option = opt || {};

    /**
     * element id
     * @property id
     * @type {String}
     */
    this.id = option.id;
    /**
     * element 
     * @property name
     * @type {String}
     */
    this.name = option.name || 'unnamed';
    /**
     * element  layer 
     * @property layer
     * @type {Object}
     */
    this.layer = option.layer;  // layer
    /**
     * element control   jQuery base element
     * @property controlBase
     * @type {Object}
     */
    this.controlBase = option.controlBase;  // text layer
    /**
     * shape  group id
     * @property group
     * @type {String}
     */
    this.group = option.group.id;  // group
    /**
     * shape   
     * @property fill
     * @type {String}
     */
    this.fill = option.fill || '#bdbdbd'; // fill
    /**
     * shape  
     * @property stroke
     * @type {String}
     */
    this.stroke = option.stroke || '#000000'; // stroke
    /**
     * shape  
     * @property strokeWidth
     * @type {Number}
     */
    this.strokeWidth = option.strokeWidth || 1;
    /**
     * polygon  shape   path position 
     * @property paths
     * @type {Array}
     */
    this.paths = option.paths || [];  // path array
    /**
     * shape   x 
     * @property posx
     * @type {Number}
     */
    this.posx = option.posx;  // center x
    /**
     * shape   y 
     * @property posy
     * @type {Number}
     */
    this.posy = option.posy;  // center y
    /**
     * shape 
     * @property width
     * @type {Number}
     */
    this.width = option.width;  // width
    /**
     * shape 
     * @property height
     * @type {Number}
     */
    this.height = option.height;  // height
    /**
     * control base shape    
     * @property showText
     * @type {Boolean}
     */
    this.showText = option.showText || true;  // show text
    /**
     * html shape      
     * @property textHtml
     * @type {String}
     */
    this.textHtml = option.textHtml;  // text html
    /**
     * polygon   x 
     * @property x1
     * @type {Number}
     */
    this.x1 = option.x1; // x1
    /**
     * polygon   x 
     * @property x2
     * @type {Number}
     */
    this.x2 = option.x2; // x2
    /**
     * polygon   y 
     * @property y1
     * @type {Number}
     */
    this.y1 = option.y1; // y1
    /**
     * polygon   y 
     * @property y2
     * @type {Number}
     */
    this.y2 = option.y2; // y2

    if (this.type === 'polygon') {
      /**
       *  x 
       * @property centerx
       * @type {Number}
       */
      this.centerx = this.posx;
      /**
       *  y 
       * @property centery
       * @type {Number}
       */
      this.centery = this.posy;
    } else {
      /**
       *  x 
       * @property centerx
       * @type {Number}
       */
      this.centerx = this.posx + (this.width / 2);
      /**
       *  y 
       * @property centery
       * @type {Number}
       */
      this.centery = this.posy + (this.height / 2);
    }
  }

  /**
   * x,y ,   .
   * @memberof Base
   * @function getTotalInfo
   * @returns {Object}  
   * @return {Number} x x
   * @return {Number} y y
   * @return {Number} width 
   * @return {Number} height 
   */
  getTotalInfo() {
    return {
      x: this.posx,
      y: this.posy,
      width: this.width,
      height: this.height
    };
  }

  /**
   *   .
   * @memberof Base
   * @function getSize
   * @returns {Object}  
   * @return {Number} width 
   * @return {Number} height 
   */
  getSize() {
    return {
      width: this.width,
      height: this.height
    };
  }

  /**
   *   .
   * @memberof Base
   * @function getPosition
   * @returns {Object}  
   * @return {Number} x x 
   * @return {Number} y y 
   */
  getPosition() {
    return {
      x: this.posx,
      y: this.posy
    };
  }

  /**
   *   .
   * @memberof Base
   * @function getCenterPosition
   * @return {Number} x center x position
   * @return {Number} y center y position
   */
  getCenterPosition() {
    return {
      x: this.centerx,
      y: this.centery
    };
  }

  /**
   *   .
   * @memberof Base
   * @function getTextInfo
   * @return {Number} x text x
   * @return {Number} y text y
   * @return {String} label label string
   */
  getTextPosition() {
    return {
      x: this.posx,
      y: this.posy,
      label: ''
    };
  }
}

export default Base;