/**
 *  
 * @name Link
 */
class Link {
  constructor(opt) {
    Object.assign(this, opt);

    if (!opt.stroke) {
      this.stroke = '#6799FF';
    }

    if (!opt.strokeWidth) {
      this.strokeWidth = 2;
    }

    if (!opt.strokeStyle) {
      this.strokeStyle = 'full';
    }

    if (!opt.strokeDash) {
      this.strokeDash = 1;
    }
  }

  /**
   *  .
   * @memberof Link
   * @function render
   * @param {Object} opt option object
   */
  render(ctx, callback) {
    ctx.beginPath();

    // move & line
    ctx.moveTo(this.sx, this.sy);
    ctx.lineTo(this.ex, this.ey);

    // begin path
    ctx.strokeStyle = this.stroke;
    ctx.lineWidth = this.strokeWidth;

    // stroke end
    ctx.stroke();

    // close path
    ctx.closePath();

    typeof callback == 'function' && callback();
    
  }

  /**
   *   position .
   * @param {String} type type( / )
   * @param {Object} shape target  shape 
   */
  modifyPosition(type, x, y) {
    const self = this;
    
    switch(type) {
      case 'start':
        self.sx = x;
        self.sy = y;
      break;
      case 'end':
        self.ex = x;
        self.ey = y;
      break;
    }
  }
}

export default Link;